      <ul>
        <li class="about-me">
          <a href="#">About Me</a>
        </li>
        <li class="my-skills">
          <a href="#">My Skills</a>
        </li>
        <li class="my-projects">
          <a href="#">My Projects</a>
        </li>
        <li class="achievments">
          <a href="#">Achievments</a>
        </li>
